@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap);
body {
  margin: 0;
  font-family: "Noto Serif", serif;
}

.App {
  text-align: center;
  background-color: #282c34;
  box-sizing: border-box;
}

.Menu {
  position: fixed;
  top: 0;
  padding: 16px;
  background-color: #16222a;
  display: flex;
  left: 0;
  right: 0;
  z-index: 45;
}

.Menu a {
  text-decoration: none;
  color: #fff;
  flex: 1 1;
}

.Menu a:hover {
  color: #ec9f05;
}

.page {
  height: 100%;
  width: 100%;
  background-size: cover !important;
}

.pageRoot {
  padding: 32px;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  color: #fff;
  /* background-color: rgba(0, 0, 0, 0.3); */
  box-sizing: border-box;
}

@media screen and (min-width: 500px) {
  .Menu a {
    font-size: 21px;
  }
}

.myImage {
  border-radius: 200px;
  padding: 6px;
  background-color: #ec9f05;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 19%;
  margin-top: 60px;
  margin-bottom: 20px;
}
div h1 {
  color: #ec9f05;
  text-shadow: 2px 2px black;
}
.myName {
  text-align: center;
  font-size: 54px;
}
@media screen and (max-width: 900px) {
  .myImage {
    width: 30%;
  }
}

@media screen and (max-width: 500px) {
  .myName {
    font-size: 40px;
  }
}

@media screen and (min-width: 500px) {
  div h1 {
    font-size: 50px;
  }
}

.sendMsn,
.infForm {
  text-align: center;
  padding: 20px;
}

.myPadding {
  width: 100%;
  padding: 10px 10px;
  margin: 3px 0;
  display: inline-block;
  border: 2px solid #ec9f05;
  border-radius: 4px;
  box-sizing: border-box;
}

.infForm {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 0.9em;
  font-weight: bold;
  margin-top: -162px;
  text-shadow: 4px 5px black;
  color: #ec9f05;
}

.contact-form {
  /* background-color: #bee1e6; */
  padding: 10px 20px;
  border-radius: 8px;
}

p {
  font-size: 20px;
}

.myBtn {
  background-color: #ec9f05;
}

@media screen and (max-width: 500px) {
  p {
    font-size: 16px;
  }

  .myPadding {
    padding: 0;
    margin: 0;
    border: 0px solid #ccc;
  }

  .infForm {
    font-size: 36px;
  }
  h5 {
    font-size: 1rem;
  }
}

/* FOOTER */
footer {
  background: #16222a;
  /* background: -webkit-linear-gradient(59deg, #343a40, #16222a);
  background: linear-gradient(59deg, #343a40, #adb5bd); */
  color: white;
  margin-top: -218px;
}

footer a {
  color: #fff;
  font-size: 14px;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
}

footer a:hover {
  color: #fa944b;
  text-decoration: none;
}

.copy {
  font-size: 12px;
  padding: 10px;
  border-top: 1px solid #ffffff;
}

.footer-middle {
  padding-top: 1em;
  color: white;
  text-align: center;
}

/*SOCİAL İCONS*/

/* footer social icons */
ul.social-network {
  list-style: none;
  display: inline;
  margin-left: 0 !important;
  padding: 0;
}
ul.social-network li {
  display: inline;
  margin: 0 5px;
}

/* footer social icons */
.social-network a.icoFacebook:hover {
  background-color: #3b5998;
}

.social-network a.icoInstagram:hover {
  background-color: #f9844a;
}

.social-network a.icoLinkedin:hover {
  background-color: #f9c74f;
}

.social-network a.icoMobile:hover {
  background-color: #2ec4b6;
}

.social-network a.icoEnvelope:hover {
  background-color: #ff7aa2;
}

.social-network a.icoEnvelope:hover i,
.social-network a.icoMobile:hover i,
.social-network a.icoFacebook:hover i,
.social-network a.icoLinkedin:hover i,
.social-network a.icoInstagram:hover i {
  color: #fff;
}

.social-network a.socialIcon:hover,
.socialHoverClass {
  color: #44bcdd;
}

.social-circle li a {
  display: inline-block;
  position: relative;
  margin: 0 auto 0 auto;
  border-radius: 50%;
  text-align: center;
  width: 50px;
  height: 50px;
  font-size: 15px;
  margin: 10px 10px 10px 10px;
}

.social-circle li i {
  margin: 0;
  line-height: 50px;
  text-align: center;
  font-size: 34px;
}

.social-circle li a:hover i,
.triggeredHover {
  -moz-transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
  -ms--transform: rotate(360deg);
  transform: rotate(360deg);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.social-circle i {
  color: #595959;
  -webkit-transition: all 0.8s;
  transition: all 0.8s;
}

.social-network a {
  background-color: #f9f9f9;
}

@media screen and (max-width: 425px) {
  .social-circle li a {
    margin: 10px 2px;
  }
  .myPadding {
    padding-bottom: 16px;
  }
  .sendMsn,
  .infForm {
    text-align: center;
    padding: 10px;
  }
}

