.animation {
  -webkit-animation: miAnimacion 6s linear 0s infinite;
          animation: miAnimacion 6s linear 0s infinite;
  -webkit-transition: all 2s;
  transition: all 2s;
}

@-webkit-keyframes miAnimacion {
  from {
    color: red;
  }
  to {
    color: yellow;
  }
}

@keyframes miAnimacion {
  from {
    color: red;
  }
  to {
    color: yellow;
  }
}

.animation2 {
  -webkit-animation: miAnimacion2 6s linear 0s 1;
          animation: miAnimacion2 6s linear 0s 1;
}

@-webkit-keyframes miAnimacion2 {
  from {
    top: 0px;
  }
  to {
    top: 200px;
  }
}

@keyframes miAnimacion2 {
  from {
    top: 0px;
  }
  to {
    top: 200px;
  }
}
.paddingTop {
  padding: 32px;
}

.skills {
  color: black;
}

.myCard {
  background-color: white;
  box-shadow: 10px 14px 2px 0 rgba(0, 0, 0, 0.2);
  min-width: 93px;
  margin: 2px;
  padding: 10px;
}

@media screen and (max-width: 520px) {
  .paddingTop {
    margin: 0 auto; /* Added */
    float: none; /* Added */
    margin-bottom: 10px; /* Added */
    padding: 11px;
    max-width: 227px;
  }
  .myCard {
    max-width: 200px;
    padding: 0;
  }
  .skills {
    color: black;
    font-size: 1.2rem;
  }
}

/* Animation property */
h2.skill1 {
  -webkit-animation: wiggle 3s linear infinite;
          animation: wiggle 3s linear infinite;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
h2.skill2 {
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
  -webkit-animation: wiggle2 3s linear infinite;
          animation: wiggle2 3s linear infinite;
}

/* Keyframes */
@-webkit-keyframes wiggle {
  0%,
  7% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  15% {
    -webkit-transform: rotateZ(-15deg);
            transform: rotateZ(-15deg);
  }
  20% {
    -webkit-transform: rotateZ(10deg);
            transform: rotateZ(10deg);
  }
  25% {
    -webkit-transform: rotateZ(-10deg);
            transform: rotateZ(-10deg);
  }
  30% {
    -webkit-transform: rotateZ(6deg);
            transform: rotateZ(6deg);
  }
  35% {
    -webkit-transform: rotateZ(-4deg);
            transform: rotateZ(-4deg);
  }
  40%,
  100% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
}
@keyframes wiggle {
  0%,
  7% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  15% {
    -webkit-transform: rotateZ(-15deg);
            transform: rotateZ(-15deg);
  }
  20% {
    -webkit-transform: rotateZ(10deg);
            transform: rotateZ(10deg);
  }
  25% {
    -webkit-transform: rotateZ(-10deg);
            transform: rotateZ(-10deg);
  }
  30% {
    -webkit-transform: rotateZ(6deg);
            transform: rotateZ(6deg);
  }
  35% {
    -webkit-transform: rotateZ(-4deg);
            transform: rotateZ(-4deg);
  }
  40%,
  100% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
}

@-webkit-keyframes wiggle2 {
  0%,
  7% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
  15% {
    -webkit-transform: rotateZ(15deg);
            transform: rotateZ(15deg);
  }
  20% {
    -webkit-transform: rotateZ(-10deg);
            transform: rotateZ(-10deg);
  }
  25% {
    -webkit-transform: rotateZ(-0deg);
            transform: rotateZ(-0deg);
  }
  30% {
    -webkit-transform: rotateZ(-6deg);
            transform: rotateZ(-6deg);
  }
  35% {
    -webkit-transform: rotateZ(4deg);
            transform: rotateZ(4deg);
  }
  40%,
  100% {
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
  }
}

@keyframes wiggle2 {
